body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  margin: 20px 0;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 0 5px 5px 0;
  outline: none;
  padding: 10px 15px;
}

.gallery {
  grid-gap: 20px;
  max-width: 1200px;
  grid-template-columns: repeat(4, 1fr);
  margin: 0 auto;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.info {
  justify-content: space-between;
  margin-top: 10px;
  display: flex;
}

.info-item {
  flex: 1;
  margin-right: 5px;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

.notiflix-info {
  background-color: #007bff;
}

.notiflix-failure {
  background-color: #dc3545;
}

.notiflix-notify-message {
  color: #fff;
}

.notiflix-notify-message b {
  font-weight: bold;
}

